/* Pond Liners Page Styles */
.pond-liners-page {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  color: #333;
}

/* Hero Section */
.hero-section {
  background: linear-gradient(135deg, #059669 0%, #10b981 100%);
  color: white;
  padding: 80px 0;
  display: flex;
  align-items: center;
  min-height: 500px;
}

.hero-content {
  flex: 1;
  max-width: 600px;
  padding: 0 40px;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  font-weight: 700;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  opacity: 0.9;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.cta-button {
  background: #f59e0b;
  color: white;
  padding: 15px 30px;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
}

.cta-button:hover {
  background: #d97706;
  transform: translateY(-2px);
}

.secondary-button {
  background: transparent;
  color: white;
  padding: 15px 30px;
  text-decoration: none;
  border: 2px solid white;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
}

.secondary-button:hover {
  background: white;
  color: #059669;
}

.hero-image {
  flex: 1;
  text-align: center;
  padding: 0 40px;
}

.hero-image img {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

/* Liner Types Section */
.liner-types-section {
  padding: 80px 0;
  background: #f8fafc;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.liner-types-section h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #059669;
}

.liner-types-section > .container > p {
  text-align: center;
  font-size: 1.1rem;
  margin-bottom: 50px;
  color: #64748b;
}

.liner-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.liner-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.liner-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.liner-image {
  height: 200px;
  overflow: hidden;
}

.liner-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.liner-card:hover .liner-image img {
  transform: scale(1.05);
}

.liner-info {
  padding: 25px;
}

.liner-info h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #059669;
}

.liner-info > p {
  margin-bottom: 20px;
  color: #64748b;
}

.liner-features h4,
.liner-thicknesses h4,
.liner-applications h4 {
  font-size: 1.1rem;
  margin-bottom: 10px;
  color: #374151;
}

.liner-features ul,
.liner-thicknesses ul,
.liner-applications ul {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
}

.liner-features li,
.liner-thicknesses li,
.liner-applications li {
  padding: 5px 0;
  color: #6b7280;
  position: relative;
  padding-left: 20px;
}

.liner-features li:before {
  content: "✓";
  color: #10b981;
  position: absolute;
  left: 0;
  font-weight: bold;
}

.liner-thicknesses li:before {
  content: "📏";
  color: #059669;
  position: absolute;
  left: 0;
}

.liner-applications li:before {
  content: "→";
  color: #10b981;
  position: absolute;
  left: 0;
  font-weight: bold;
}

.liner-price {
  font-size: 1.3rem;
  font-weight: 700;
  color: #059669;
  margin-bottom: 20px;
}

.liner-cta {
  background: #059669;
  color: white;
  padding: 12px 25px;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
}

.liner-cta:hover {
  background: #047857;
  transform: translateY(-2px);
}

