  color: #059669;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section {
    flex-direction: column;
    text-align: center;
    padding: 60px 20px;
  }
  
  .hero-content {
    padding: 0 20px;
    margin-bottom: 40px;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .liner-grid,
  .underlayment-grid,
  .seaming-grid,
  .installation-grid {
    grid-template-columns: 1fr;
  }
  
  .sizing-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  
  .packages-grid {
    grid-template-columns: 1fr;
  }
  
  .package-card.featured {
    transform: none;
  }
  
  .package-card.featured:hover {
    transform: translateY(-5px);
  }
  
  .maintenance-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2rem;
  }
  
  .hero-content p {
    font-size: 1rem;
  }
  
  .liner-types-section h2,
  .underlayment-section h2,
  .seaming-section h2,
  .installation-section h2,
  .sizing-section h2,
  .why-choose-section h2,
  .maintenance-section h2,
  .packages-section h2,
  .contact-cta h2 {
    font-size: 2rem;
  }
  
  .liner-card,
  .underlayment-card,
  .seaming-card,
  .installation-card {
    margin: 0 10px;
  }
  
  .package-card {
    padding: 30px 20px;
  }
  
  .size-card {
    padding: 20px;
  }
}
